const axios = require("axios");

class Player {
  constructor(response) {
    this.id = response.player.id;
    const {name, familyName, givenName, nationality, homeLocation, headshot} = response.player;
    const {team} = response.player.teams[0];
    team.role = response.player.attributes;
    this.basic = {name, familyName, givenName, nationality, homeLocation, headshot};
    this.team = team;
    if (this.team.id === 4409)
      this.team.primaryColor = '000000';
    this.heroes = [];
    response.stats.heroes.forEach(hero => {
      let stats = hero.stats;
      stats.hero = hero.name;
      this.heroes.push(stats)
    });
    this.all = [];
    response.stats.all.type = "数据";
    this.all.push(response.stats.all);
    response.statRanks.type = "排名";
    this.all.push(response.statRanks);

  }
}

class OWL {
  constructor({inChina = false}) {
    this.api = axios.create({
      crossDomain: true,
      crossOrigin: true,
      baseURL: inChina === true ? 'https://api.overwatchleague.cn/' : 'https://api.overwatchleague.com/',
      params: {
        locale: inChina === true ? 'zh-cn' : 'en'
      }
    })
    ;
  }

  static get STAGE() {
    return {
      REGULAR: 'regular_season',
      POST: 'postseason'
    }
  }

  getPlayer(id, {season = 2019, stage = OWL.STAGE.REGULAR} = {}) {
    return new Promise((resolve, reject) => {
      this.api.get(`/players/${id}`, {
        params: {
          season: season,
          stage_id: stage,
          expand: 'stats,stat.ranks'
        }
      })
        .then(response => resolve(new Player(response.data.data)))
        .catch(err => reject(err))

    })

  }

  async allPlayers({season = 2019, stage = OWL.STAGE.REGULAR} = {}) {
    return new Promise((resolve, reject) => {
      this.api.get(`/stats/players`, {
        params: {
          season: season,
          stage_id: stage,
        }
      })
        .then(response => resolve(response.data.data))
        .catch(err => reject(err))
    })
  }

  async allTeams() {
    return new Promise((resolve, reject) => {
      this.api.get(`/teams`,)
        .then(response => {
          const teams = {};
          response.data.competitors.forEach(team => {
            teams[team.competitor.id] = team.competitor
          })
          resolve(teams)
        })
        .catch(err => reject(err))
    })
  }
}

export default OWL;




